import React from 'react'
import navimg from '../images/navimg.png'

const Nav = () => {
  return (
    <div>
              <nav>
        <div className="navleft">
          <img src={navimg} alt="" />
        </div>
        <div className="navright">
          <p>Home</p>
          <p>About Us</p>
          <p>Service</p>
          <p>Pricing</p>
          <p>Blog</p>

          <button>Contact Us</button>
        </div>
      </nav>
    </div>
  )
}

export default Nav